import React, { useEffect, useState } from 'react';

/* Library */
import styled from 'styled-components';

const StyledBiosTyping = styled.span`
        >div{
            /* height: 500px; */
            span{
                animation: cursor 1s step-end infinite;
                animation-duration: 0.3s; 
                animation-iteration-count: 35;
                @keyframes cursor{
                    0%{border-right: 5px solid #fff} 
                    50%{border-right: 5px solid #000} 
                    100%{border-right: 5px solid #fff} 
                }
            }
        }
        white-space: pre-wrap;
        line-height: 1.5;
`

const BiosTyping = () => {
  /* const txt = `.    . \n      .\n    IT      , \n     .\n           .\n       .\n PORTFOLIO98\n  .` */
  const txt = `Booting...   ...\n$: > Total memory found: 256kb\n$: > Scanning for     100%\n$: > Scanning for     100%\n$: > Scanning for     100%\n    ...\n\n!     .\n\n           .\n\nHTML, CSS, JavaScript, React         .\n\n      ,     .\n\n           .\n\n PORTFOLIO98   .`
  /* $: > Scanning for     100% */
  /*   $: > Booting systems...
  $: > Total memory found: 256kb
  $: > Scanning for bootable media     100%
  $: > No media found. Booting from network address 3E:40:9C:75
  $: > Loading BIOS 2.3                100%
  $: > Reticulating splines            100%
  $: > Booting console                 100%
  $:
  $: > Welcome to the retro console version 0.1a
  $:
  $: > Hello Designer023. How can I help you today?
  $: I can has matrix?
  $: > buffering matrix... 20%
  $: > buffering matrix... 66%
  $: > buffering matrix... 100%
  $: > So, would you like a red or blue pill? 
  ? (Y/N)  
  */
  
  const [Text, setText] = useState('');
  const [Count, setCount] = useState(0);
  
  useEffect(() => {
    let a=true;
    const interval = setInterval(() => {
        setText(Text + txt[Count]); //  set  +  
        setCount(Count + 1); //    
    }, 10);
    if(Count === txt.length)  {  // Count    Text.length  
        clearInterval(interval);//    setInterval 
        a=false;
        console.log(a);
        if(a===false){
          /*    useRef  input focus ..   */
        }
    }
    console.log(useEffect);
    return () => {clearInterval(interval)}; //  setInterval 
  })
  
  return (
  	<StyledBiosTyping className="text">
        <div>
            <span className='typing'>{ Text }</span>
        </div>
    </StyledBiosTyping>
  )
}

export default BiosTyping;